<template>
  <div class="container max-w-4xl my-10">
    <div class="bg-white rounded px-1 pt-6 pb-8 mb-4">
      <h1 class="text-xl text-center pb-3">{{ title }}</h1>
      <div class="flex-col">
        <div class="flex flex-row">
          <div class="w-5/6">
            <video id="video" ref="videoPlayer" crossorigin="anonymous"></video>
          </div>
          <div
            class="flex flex-no-wrap flex-col w-1/6 ml-3 border-gray-200 border-1"
          >
            <div
              v-for="(k, index) in keyEvents"
              class="moment"
              :key="index"
              v-on:click="player.currentTime = k.videoTime"
            >
              <span class="inline-block">{{ k.friendly_name }}</span>
            </div>
          </div>
        </div>
        <div>
          <canvas class="hidden" id="canvas"></canvas>
        </div>

        <div
          v-if="ActiveWebVTT"
          class="overflow-x-scroll text-xs mt-3 whitespace-pre"
        >
          <p>{{ webvtt }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//import videojs from "video.js";
import Plyr from "plyr";
import shaka from "shaka-player/dist/shaka-player.compiled.js";
import muxjs from "mux.js";

export default {
  name: "VideoPlayer",

  props: {
    videoOptions: {
      type: Object,
      default() {
        return {};
      },
    },
  },

  data() {
    return {
      displayTime: "00:00:00",
      playState: true,
      isFullscreen: false,
      progress: "height: 10px;width: 0%",
      ActiveWebVTT: false,
      calbrateData: [],
      ActiveCalibrate: false,
      bcData: {},
      urlprefix: "ffa",
      video: null,
      webvtt: "",
      matchOffsetData: null,
      canvas: null,
      ctx: null,
      keyEvents: [],
      title: null,
      matchInfo: {},
      matchId: null,
      currentTime: 0,
      matchData: {},
      player: null,
      options: {
        autoplay: false,
        controls: false,
      },
      playerKey:
        "BCpkADawqM2Hd4WyWQyds9y5MrKtYzkxP3PfPT_i5BflGWW5W2XurnU1zrdrfp44fqkv0dkzrPGUVSuk-HBHUvt7CySWQNux7SvwmNgNzmjrtpSz7EB7fvdr0cq2OgMCl37NMo8pLo-K4Uu8",
    };
  },
  async mounted() {
    let urlParams = new URLSearchParams(window.location.search);
    //For Audio manipulation

    window.muxjs = muxjs;

    this.canvas = document.getElementById("canvas");
    this.video = document.getElementById("video");
    this.ctx = this.canvas.getContext("2d");
    this.bcData = await this.getBC(urlParams.get("videoId"));
    this.urlprefix = urlParams.get("platform") == "stg" ? "ffastg" : "ffa";
    console.log(urlParams.get("platform"));
    this.title = this.bcData.name;
    this.matchId = this.bcData.custom_fields.matchid;

    if (this.bcData.sources.length > 1) {
      this.options.sources = this.bcData.sources.filter((v) => {
        return v.src.startsWith("https://");
      });
    } else {
      this.options.sources = this.bcData.sources;
    }
    this.matchData = await this.getMatchData(urlParams.get("videoId"));
    this.keyEvents = [
      { q: 1, videoTime: this.matchData.q1, friendly_name: "Quarter 1" },
      { q: 2, videoTime: this.matchData.q2, friendly_name: "Quarter 2" },
      { q: 3, videoTime: this.matchData.q3, friendly_name: "Quarter 3" },
      { q: 4, videoTime: this.matchData.q4, friendly_name: "Quarter 4" },
    ];
    this.initApp();
  },
  beforeDestroy() {},
  methods: {
    onErrorEvent(event) {
      // Extract the shaka.util.Error object from the event.
      this.onError(event.detail);
    },

    onError(error) {
      // Log the error.
      console.error("Error code", error.code, "object", error);
    },
    async initPlayer() {
      // Create a Player instance.
      const video = document.getElementById("video");
      const player1 = new Plyr(video);
      window.player = player1;

      const player = new shaka.Player(video);
      this.player = player1;

      // Listen for error events.
      player.addEventListener("error", this.onErrorEvent);

      // Try to load a manifest.
      // This is an asynchronous process.
      try {
        console.log(this.options.sources[0].src);
        await player.load(this.options.sources[0].src);

        // This runs if the asynchronous load is successful.
        console.log("The video has now been loaded!");
      } catch (e) {
        // onError is executed if the asynchronous load fails.
        this.onError(e);
      }
    },
    initApp() {
      // Install built-in polyfills to patch browser incompatibilities.
      shaka.polyfill.installAll();

      // Check to see if the browser supports the basic APIs Shaka needs.
      if (shaka.Player.isBrowserSupported()) {
        // Everything looks good!
        this.initPlayer();
      } else {
        // This browser does not have the minimum set of APIs we need.
        console.error("Browser not supported!");
      }
    },
    getMatchData(videoId) {
      return new Promise((resolve, reject) => {
        var requestOptions = {
          method: "GET",
          redirect: "follow",
        };

        fetch(
          `https://bkmmwpjq2d.execute-api.ap-southeast-2.amazonaws.com/netball/${videoId}.json`,
          requestOptions
        )
          .then((response) => response.json())
          .then((result) => resolve(result))
          .catch((error) => reject(error));
      });
    },
    getBC(videoId) {
      return new Promise((resolve, reject) => {
        var myHeaders = new Headers();
        myHeaders.append(
          "Accept",
          `application/json;pk=BCpkADawqM2Hd4WyWQyds9y5MrKtYzkxP3PfPT_i5BflGWW5W2XurnU1zrdrfp44fqkv0dkzrPGUVSuk-HBHUvt7CySWQNux7SvwmNgNzmjrtpSz7EB7fvdr0cq2OgMCl37NMo8pLo-K4Uu8`
        );

        var requestOptions = {
          method: "GET",
          headers: myHeaders,
          redirect: "follow",
        };

        fetch(
          `https://edge.api.brightcove.com/playback/v1/accounts/6057984925001/videos/${videoId}`,
          requestOptions
        )
          .then((response) => response.json())
          .then((result) => resolve(result))
          .catch((error) => reject(error));
      });
    },
  },
};
</script>

