  /* override other styles to make responsive */
  width: inherit !important;
  height: inherit !important;
}
.moment {
  @apply border text-xs text-center;
}
.overlay {
  position: absolute;
  width: 100%;
  fill: black;
  height: 100%;
  z-index: 1;
  bottom: 0;
}
</style>

